// import GlobalSettings from '@/GlobalSettings'
import { IKDataEntity } from 'metaflow-js'
import hillo from 'hillo'
import GlobalSettings from '@/GlobalSettings'
import StringEntity from '@/model/StringEntity'

const entity = {
  id: {
    type: IKDataEntity.Types.Integer,
    displayName: 'id',
    tableConfig: {
      align: 'start',
    },
    formConfig: {
      requiredNew: false,
    },
  },
  langs: {
    displayName: 'name',
    type: IKDataEntity.Types.Group,
    formConfig: {
      groupName: 'name',
      default: GlobalSettings.supportLang,
      childLabelKey: 'lang',
      type: {},
    },
    tableConfig: {
      displayCondition: (item) => {
        return item.lang.toUpperCase() === GlobalSettings.getLang()
      },
      displayChild: (key) => {
        return key === 'name'
      },
    },
    childKey: ['name', 'desc'],
    labelKey: 'lang',
    children: [
      StringEntity, StringEntity, StringEntity,
    ],
    header: false,
  },
  name: {
    displayName: 'shownName',
    form: false,
  },
  printName: {
    displayName: 'printTitle',
  },
  zbonGroup: {
    displayName: 'zbonGroup',
  },
  color: {
    type: IKDataEntity.Types.Color,
    formConfig: {
      required: false,
    },
  },
}
const config = {
  load: async function (filter) {
    const response = (await hillo.get('Complex.php', {
      op: 'showAllConsumeTypeInfo',
      lang: GlobalSettings.getLang(),
      ...filter,
    }))
      .content
    console.log(response, 'response')
    response.forEach(i => {
      i.langs = Object.values(i.langs)
    })
    return response
  },
  add: function (item) {
    item.name = item.langs[2].name
    // hard code: displayName设置为德语
    return hillo.post('ConsumeType.php?op=add', {
      ...item,
      langs: JSON.stringify(item.langs),
    }, {
      showLoading: true,
    })
  },
  edit: function (item) {
    item.name = item.langs[2].name
    // hard code: displayName设置为德语
    return hillo.postWithUploadFile('ConsumeType.php?op=update', {
      ...item,
      langs: JSON.stringify(item.langs),
    }, {
      showLoading: true,
    })
  },
  remove: function (id) {
    return hillo.post('ConsumeType.php?op=delete', { id: id })
  },
}

export default IKDataEntity.ModelFactory(entity, config)
